.js-iframe-defer {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.loading-window {
  height: 400px;
  width: 400px;
}

.ld-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  background: rgba(17, 17, 17, 0.17);
}

.ld-spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  display: inline-block;
  width: 80px;
  height: 80px;
}
.ld-spinner::after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #fff;
  border-color: #fff transparent #fff transparent;
  -webkit-animation: ld-dual-ring 1.2s linear infinite;
  animation: ld-dual-ring 1.2s linear infinite;
}

@-webkit-keyframes ld-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes ld-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.js-change-response {
  display: block;
  position: absolute;
  width: 100vw;
  height: 100vh;
}

.change-mask {
  display: block;
  width: 100%;
  height: 100%;
  background: rgba(57, 66, 83, 0.23);
  position: fixed;
  z-index: 7;
}

.change-wrapper {
  z-index: 8;
  border-radius: 4px;
  background: rgba(255, 255, 250, 0.76);
  box-shadow: 3px 3px 8px black;
  position: fixed;
  max-width: 500px;
  right: 50%;
  bottom: 50%;
  overflow: hidden;
  transform: translateX(50%) translateY(50%);
}

.change-current {
  padding: 1rem;
  background: lightgray;
}

.change-available {
  height: 500px;
  padding: 1rem;
  background: white;
}
.change-available ul {
  overflow-y: scroll;
  height: 400px;
  margin: 0 auto;
}
.change-available ul li {
  list-style-type: none;
}
.change-available ul li button {
  text-align: right;
  width: 100%;
}

.admin-content {
  position: relative;
  padding: 1rem;
  overflow-y: scroll;
}

.admin-scroll {
  overflow-x: scroll;
}

.filters {
  padding-bottom: 1rem;
}
.filters form ul {
  display: flex;
  flex-direction: column;
}
@media only screen and (min-width: 768px) {
  .filters form ul {
    flex-direction: row;
  }
}
.filters form ul li {
  max-width: 300px;
}
.filters form ul li label {
  width: 100%;
}
.filters form .button {
  display: block;
}

.admin-table {
  width: 100%;
}
.admin-table th {
  text-align: left;
  padding: 1rem;
}
.admin-table tr {
  border-top: 1px dashed #e6e6e6;
}
.admin-table tr td {
  padding: 0.6rem 1rem;
}

.admin-form_as_table th {
  padding: 0.1px;
  display: inline-block;
  width: 100%;
}
.admin-form_as_table th label {
  padding: 0.3px;
  width: 100%;
}
.admin-form_as_table td {
  display: inline-block;
  width: 100%;
  padding: 0.1px;
}
.admin-form_as_table td input {
  padding: 6px;
}
@media only screen and (min-width: 576px) {
  .admin-form_as_table th {
    display: table-cell;
    width: auto;
  }
  .admin-form_as_table td {
    display: table-cell;
    width: auto;
  }
}

.hover-info {
  display: block;
  height: 100%;
  width: 100%;
  position: relative;
}
.hover-info .hover-info__list {
  display: none;
}
.hover-info:hover .hover-info__list {
  display: block;
}

.hover-info__list {
  top: 0;
  padding: 1rem;
  background: white;
  min-width: 300px;
  position: absolute;
  box-shadow: 3px 3px 8px black;
  border-radius: 4px;
  left: 50%;
  transform: translate(-50%);
  z-index: 9;
}
.hover-info__list li {
  list-style-type: none;
}
.hover-info__list li button {
  display: block;
  width: 100%;
}

button {
  padding: 0.5rem 1rem;
  min-width: 100px;
}
button:hover {
  background: lightblue;
  cursor: pointer;
}